.Toolbar {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #703b09;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0  20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 90;
  -webkit-box-shadow: 0 14px 26px -12px rgba(112, 59, 9, .42), 0 4px 23px 0 rgba(112, 59, 9, .12), 0 8px 10px -5px rgba(112, 59, 9, .2);
          box-shadow: 0 14px 26px -12px rgba(112, 59, 9, .42), 0 4px 23px 0 rgba(112, 59, 9, .12), 0 8px 10px -5px rgba(112, 59, 9, .2);
}

.Toolbar nav {
  height: 100%;
}

.Logo {
  height: 80%;
}

@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}



