.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 4px;
    padding: 16px;
    left: 15%;
    top: 30%;
    outline: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: opacity .15s linear, -webkit-transform 0.3s ease-out;
    transition: opacity .15s linear, -webkit-transform 0.3s ease-out;
    -o-transition: transform 0.3s ease-out, opacity .15s linear;
    transition: transform 0.3s ease-out, opacity .15s linear;
    transition: transform 0.3s ease-out, opacity .15s linear, -webkit-transform 0.3s ease-out;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}



